import styles from "./CopyEmail.module.css";
import copyIcon from "../../assets/hero/copy-icon.svg";
import PPIUtils from "../../utils/ppiUtils";

const CopyEmail = () => {

    const copyToClipBoard =  () => {
        navigator.clipboard.writeText(PPIUtils.emailAddress);
        alert("Email Copied!");
    }

    return  (<div className={styles.getEmailContainer} onClick={copyToClipBoard}>
                <span className={styles.getEmail}>Copy email address</span> <img src={copyIcon} alt="Click to copy" className={styles.copyIcon}/> 
            </div>);
}

export default CopyEmail;