import SimpleCrypto from "simple-crypto-js";


// Encrypting simply to avoid bots scrapping github repositories 
// and finding plain text PPI to add to some mail lists,..
class PPIUtils {
    static _key = "1010101010";
    static email = "c13e822bb2a4d757f54d2fe3e6ac1ee8dcbc2880b73cad2ce6fbe578db7e7c0a98vLfjaAVNz+Z3hSRLR83zbw8HtfvItlBItARNI9vr0=eca8b5beb98c8b948d813c0689ec8a9f00a71a97374f44439bada07aa437811a";
    

    static encrypt(a) {
        var simpleCrypto = new SimpleCrypto(this._key);
        var chiperText = simpleCrypto.encrypt(a);
        return chiperText;
    }

    static decrypt(chiperText) {
        var simpleCrypto = new SimpleCrypto(this._key);
        var plainText = simpleCrypto.decrypt(chiperText);
        return plainText;
    }

    static get emailAddress()  {
        return this.decrypt(this.email);
    }


}

export default PPIUtils;